import React from 'react';
import { Link } from 'react-router-dom'

export default class TimelineNav extends React.Component {
	render() {
		return (
			<div className="container">
				<ul className="navigation">
					<Link to="/"><li className="navigation__item first-item">Timeline</li></Link>
					<Link to="/about"><li className="navigation__item">About</li></Link>
					<li className="navigation__item">Friends</li>
					<li className="navigation__item">Photos</li>
					<li className="navigation__item">More</li>
				</ul>
			</div>
		);
	}
}


