import React from 'react';
import PostBox from './PostBox';
import Post from './Post';


const RightProfile = () => {
	return (
		<div className="right-profile-container">
			<PostBox />
			<Post 
				date={"October 22, 2017"}
				likes={"Amy Han and Jae Hee Han like this"}
				content={
					<p className="top-post__content"> 
						This program shows "Mario" learning to grab all the coins and kill all the 
						enemies in this homemade simulator. Mario learns using machine learning algorithms 
						NEAT and Novelty search. Check out a video of this here!
					</p>
				}
			/>

			<Post 
				date={"May 5, 2017 4:41pm"}
				likes={"Bill Gates, Mark Zuckerberg, and 3 others like this"}
				content={
					<p className="top-post__content"> 
						This program simulates gravity in two dimensions. The user can add masses to the system, 
						changing their mass and initial velocities at will. Check out a video of this here!
					</p>
				}
			/>
		</div>
	)
}

export default RightProfile;


