import React from 'react';
import AboutTab from './AboutTab';
import AboutTitle from './AboutTitle';

import eic from '../images/eic-icon.png';
import swat from '../images/swarthmore-icon.jpg';
import exelon from '../images/exelon-icon.jpg';
import sf from '../images/squadfusion-icon.jpg';
import spotify from '../images/spotify-icon.jpg';


const Work = () => {
	return (
		<div className="about-container">
			<AboutTitle title={"WORK"} />
			<AboutTab 
				imgsrc={eic} 
				title={"New Pathway"} 
				subtitle={"Math/Science Teacher · August 15, 2017 to present"} 
				description={
					<p>
						I teach SAT/ACT Math, ACT Science, and SAT 2 Physics and Math
					</p>
				} />
			<hr className="about__hr"/>
			<AboutTab 
				imgsrc={swat} 
				title={"Swarthmore College"} 
				subtitle={"Plasma Physics Research Assistant · Summer of 2016"} 
				description={
					<p>
						Analyzed data using Python to compute the density and velocity of plasma based on the 
						input voltage signals and produced graphs that my team presented at the National Plasma
						Physics Conference
						<br/>
						<br/>
						Oversaw the optical laser interferometry system to maximize signal on detectors and ensure 
						proper alignment of the lasers
						<br/>
						<br/>
						Helped construct various boundary conditions, which was our independent variable, for
						the experimental design, as well as the probes used to detect the magnetic field
					</p>
				} /> 
			<hr className="about__hr"/>
			<AboutTab 
				imgsrc={exelon} 
				title={"Exelon Inc."} 
				subtitle={"Technical Intern · Summer of 2015"} 
				description={
					<p>
						Worked with my manager to smoothly execute transition from Application Lifecycle Management (ALM) Version 11.52 to Version 12.02
						<br/>
						<br/>
						Wrote a powershell script to automate repetitive work
					</p>
				} />
			<hr className="about__hr"/>
			<AboutTab 
				imgsrc={sf} 
				title={"SquadFusion Inc."} 
				subtitle={"Business Development Intern · Fall of 2015"} 
				description={
					<p>
						Researched competing companies and created a matrix with an overview analysis highlighting their various features, such as social media presence and customer service
					</p>
				} /> 
			<hr className="about__hr"/>
			<AboutTab 
				imgsrc={spotify} 
				title={"Spotify"} 
				subtitle={"Data Analyst Extern · January 2015"} 
				description={
					<p>
						Analyzed data of streamed music during Christmas Week, highlighting interesting trends and presented my findings to the Data Analytics department
						<br/>
						<br/>
						Developed visualizations on the shifts in stream sources from music artists Led Zeppelin and Pink Floyd
					</p>
				} />


			<AboutTitle title={"PROFESSIONAL SKILLS"} />
			<p>
				Python, Javascript, HTML/CSS, Node, Express, React, Redux, MongoDB
			</p>

			<AboutTitle title={"COLLEGE"} />
			<AboutTab 
				imgsrc={swat} 
				title={"Swarthmore College"} 
				subtitle={"Class of 2017 · Computer Science · Physics · Swarthmore, Pennsylvania"}  /> 

		</div>
	)
}

export default Work;


