import React from 'react'

const AboutTab = (props) => {
	return (
		<div className='about__tab'>
			<img className="about__img" src={props.imgsrc} />
			<div className="about__text">
				<p className="about__title">{props.title}</p>
				<p className="about__subtitle">{props.subtitle}</p>

				<p className="tab__description">
					{props.description}
				</p>

			</div>
		</div>
	)
}

export default AboutTab;


