import React from 'react'
import Header from './Header.js'
import Cover from './Cover.js'
import Body from './Body.js'

export default class Profile extends React.Component {
	render() {
		return (
			<div>
				<div className="profile">
					<Header />
					<Cover />
				</div>
				<Body />
			</div>
		)
	}
}



