import React from 'react'
import AboutTab from './AboutTab';
import AboutTitle from './AboutTitle';

import sh from './../images/shanghai-icon.jpg';
import la from './../images/la-icon.jpg';
import swat from './../images/swarthmore-icon.jpg';
import ak from './../images/anchorage-icon.jpg';
import ny from './../images/ny-icon.jpg';


const Places = () => {
	return (
		<div className="about-container">
			<AboutTitle title={"CURRENT CITY AND HOMETOWN"} />
			<AboutTab imgsrc={sh} title={'Shanghai, China'} subtitle={'Current city'}/>
			<hr className="about__hr"/>
			<AboutTab imgsrc={la} title={'Cypress, California'} subtitle={'Hometown'}/>
			<AboutTitle title={"OTHER PLACES LIVED"} />
			<AboutTab imgsrc={swat} title={'Swarthmore, Pennsylvania'} subtitle={'August 2013 to May 2017'}/>
			<hr className="about__hr"/>
			<AboutTab imgsrc={ak} title={'Anchorage, Alaska'} subtitle={'1997 to 2005'}/>
			<hr className="about__hr"/>
			<AboutTab imgsrc={ny} title={'New York, New York'} subtitle={'1994 to 1997'}/>
		</div>
	)
}

export default Places;


