import React from 'react';

import prof from './../images/prof-pic-small.jpg';
import cmt from './../images/prof-pic-comment.jpg';
import like from './../images/like-button.PNG';
import comment from './../images/comment-button.PNG';
import share from './../images/share-button.PNG';
import liked from './../images/liked-button.PNG';

const Post = (props) => {
	return (
		<div className="right-container">
			<div className="top-post">
				<div className="top-post__header">
					<img className="top-post__img" src={prof}/>
					<div className="top-post__name-date">
						<h3 className="top-post__name">Jeremy Han</h3>
						<p className="top-post__date">{props.date}</p>
					</div>
				</div>

				{props.content}

				<hr className="post__hr"/>
				<ul className="top-post__buttons">
					<li className="top-post__button"><img className="post-button__img" src={like}/>Like</li>
					<li className="top-post__button"><img className="post-button__img" src={comment}/>Comment</li>
					<li className="top-post__button"><img className="post-button__img" src={share}/>Share</li>
				</ul>
			</div>

			<div className="bottom-post">
				<p className="bottom-post__likes"><img className="liked__img" src={liked}/> {props.likes}</p>
				<form onSubmit={(e) => {e.preventDefault()}}className="bottom-post__add-comment">
					<img className="bottom-post__img" src={cmt}/>
					<input 
						className="bottom-post__input"
						placeholder="Write a comment.."/>
				</form>
			</div>



		</div>
	);
}

export default Post;


