import React from 'react';
import world from './../images/fb-earth-logo.PNG';
import work from './../images/work-logo.png';
import home from './../images/home-logo.png';
import current from './../images/current-logo.png';

const Intro = () => {
	return (
		<div className="left-container intro">
			<img className="intro__world" src={world}/>
			<h3 className="intro__header">
				Intro
			</h3>

			<p className="intro__quip">
				<a>Work Experience</a><br/>
				<a href="https://github.com/jeremyhan111">Github</a><br/>
				jeremyhan111@gmail.com
			</p>

			<hr className="intro__hr"/>

			<ul className="intro__list">
				<li className="intro__list-item">
					<div className="intro__img-container">
						<img className="intro__img" src={work}/>
					</div>
					Studied Physics and Computer Science at Swarthmore College
				</li>
				<li className="intro__list-item">
					<div className="intro__img-container">
						<img className="intro__img" src={work}/>
					</div>
					Math/Science Teacher at New Pathway
				</li>
				<li className="intro__list-item">
					<div className="intro__img-container">
						<img className="intro__img" src={current}/>
					</div>
					Lives in Shanghai
				</li>
				<li className="intro__list-item">
					<div className="intro__img-container">
						<img className="intro__img" src={home}/>
					</div>
					From Cypress, California
				</li>
			</ul>

		</div>
	)
}

export default Intro;


