import React from 'react';
import AboutTab from './AboutTab';

import eic from '../images/eic-icon.png';
import swat from '../images/swarthmore-icon.jpg';
import shanghai from '../images/shanghai-icon.jpg';

const Overview = () => {
	return (
		<div className="about-container overview__container">
			<AboutTab imgsrc={eic} title={"Works at EIC New Pathway"}/>
			<hr className="about__hr overview__hr"/>
			<AboutTab imgsrc={swat} title={"Studied Physics and Computer Science at Swarthmore College"} subtitle={"Class of 2017"} />
			<hr className="about__hr overview__hr"/>
			<AboutTab imgsrc={shanghai} title={"Lives in Shanghai, China"} subtitle={"From Cypress, California"} />
		</div>
	);
};

export default Overview;


