import React from 'react';
import BasicInfo from './BasicInfo';
import AboutTitle from './AboutTitle';

const Contact = () => {
	return (
		<div className="about-container">
			<AboutTitle title={"CONTACT INFORMATION"} />
			<BasicInfo type={'Email'} description={'jeremyhan111@gmail.com'}/>
			<hr className="about__hr"/>
			<BasicInfo type={'WeChat'} description={'jeremyhan111'}/>
			<hr className="about__hr"/>
			<BasicInfo type={'KakaoTalk'} description={'jeremyhan111'}/>

			<AboutTitle title={"WEBSITES AND SOCIAL LINKS"} />
			<BasicInfo type={'Github'} description={'github.com/jeremyhan111'}/>

			<AboutTitle title={"BASIC INFORMATION"} />
			<BasicInfo type={'Birth Date'} description={'November 1'}/>
			<hr className="about__hr"/>
			<BasicInfo type={'Birth Year'} description={'1994'}/>
			<hr className="about__hr"/>
			<BasicInfo type={'Gender'} description={'Male'}/>
			<hr className="about__hr"/>
			<BasicInfo type={'Languages'} description={'English, Mandarin, Korean'}/>
		</div>
	)
}

export default Contact;


