import React from 'react'
import logo from '../images/fb-logo.jpg'
import prof from '../images/prof-pic-small.jpg'

const handleSubmit = (e) => {
	e.preventDefault();
}

const Header = () => (
	<div className="header">
		<div className="container">
			<form onSubmit={handleSubmit} className="header__form">
				<div>
					<img src={logo} className="header__fb-image"/>
					<input className="header__input" type="text" name="name"/>
					<button className="header__button">Search</button>
				</div>
				<img src={prof}className="header__prof-pic"/>
			</form>
		</div>
	</div>
);

export default Header;


