import React from 'react';

const Pictures = (props) => {
	return (
		<div className="left-container pictures">
			<img className="intro__world" src={props.icon}/>
			<h3 className="intro__header">
				{props.title}
			</h3>

			<div className="picture-rows">
				<div className="picture-row">
					<img className="row__img" src={props.pic1} />
					<img className="row__img" src={props.pic2} />
					<img className="row__img" src={props.pic3} />
				</div>

				<div className="picture-row">
					<img className="row__img" src={props.pic4} />
					<img className="row__img" src={props.pic5} />
					<img className="row__img" src={props.pic6} />
				</div>

				<div className="picture-row">
					<img className="row__img" src={props.pic7} />
					<img className="row__img" src={props.pic8} />
					<img className="row__img" src={props.pic9} />
				</div>

			</div>

		</div>
	)
}

export default Pictures;


