import React from 'react';
import AboutTitle from './AboutTitle';
import AboutTab from './AboutTab';

const SelfDetails = () => {
	return (
		<div className="about-container">
			<AboutTitle title={'ABOUT YOU'} />
			<p> 
				Hello! My name is Jeremy Han, a recent college graduate. I was fortunate enough to attend Swarthmore College, 
				where I graduated with Honors in Physics and Computer Science. I am currently teaching SAT/ACT in Shanghai and 
				diligently studying Mandarin, Data Science, and Machine Learning during my free time. Check out my github page to 
				look at my programming projects!
			</p>
			<AboutTitle title={'FAVORITE QUOTES'} />
			<p>
				Clear prose is like a window pane. <br/>
				George Orwell <br/>
				<br />
				More Cowbell! <br/>
				The Bruce Dickinson <br/>
			</p>


		</div>
	)
}

export default SelfDetails;


