import React from 'react';
import prof from './../images/prof-pic-small.jpg'

const handleSubmit = (e) => {
	e.preventDefault();
	console.log("thanks for posting!")
}

const PostBox = () => {
	return (
		<div className="right-container postbox">
			<ul className="post-options">
				<li className="post-options__option">Create a Post</li>
				<li className="post-options__option">Photo/Video Album</li>
				<li className="post-options__option">Live Video</li>
				<li className="post-options__option last">Life Event</li>
			</ul>

			<form onSubmit={handleSubmit}>
				<img className="postbox__img"src={prof}/>
				<textarea 
					className="postbox__textarea" 
					placeholder="What's on your mind?">
				</textarea>
			</form>
			<hr className="postbox__hr"/>
		</div>
	)
}

export default PostBox;


