import React from 'react';
import Intro from './Intro'
import Pictures from './Pictures';

import photoicon from '../images/photos-icon.PNG';
import friendsicon from '../images/friends-icon.PNG';

import pic1 from '../images/photo-1.jpg';
import pic2 from '../images/photo-2.jpg';
import pic3 from '../images/photo-3.jpg';
import pic4 from '../images/photo-4.jpg';
import pic5 from '../images/photo-5.jpg';
import pic6 from '../images/photo-6.jpg';
import pic7 from '../images/photo-7.jpg';
import pic8 from '../images/photo-8.jpg';
import pic9 from '../images/photo-9.jpg';

import elon from '../images/elon.jpg';
import ndt from '../images/ndt.jpg';
import choi from '../images/choi.jpg';
import neumann from '../images/neumann.gif';
import mark from '../images/mark.jpg';
import popper from '../images/popper.jpg';
import maxwell from '../images/maxwell.jpg';
import bruce from '../images/bruce.jpg';
import rick from '../images/rick.jpg';

const LeftProfile = () => {
	return (
		<div className="left-profile-container">
			<Intro />
			<Pictures 
				title={'Photos'} 
				icon={photoicon} 
				pic1={pic1}
				pic2={pic2}
				pic3={pic3}
				pic4={pic4}
				pic5={pic5}
				pic6={pic6}
				pic7={pic7}
				pic8={pic8}
				pic9={pic9}
			/>

			<Pictures 
				title={'Friends'} 
				icon={friendsicon} 
				pic1={elon}
				pic2={ndt}
				pic3={choi}
				pic4={neumann}
				pic5={mark}
				pic6={popper}
				pic7={maxwell}
				pic8={bruce}
				pic9={rick}/>
		</div>
	)
}

export default LeftProfile;


